/* 深色模式全局样式 */
.dark-mode {
  background-color: var(--background-color);
  color: var(--text-color);
}

/* 确保深色模式下的文本颜色 */
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h5, .dark-mode h6 {
  color: var(--text-color);
}

/* 确保深色模式下的链接颜色 */
.dark-mode a {
  color: var(--primary-color);
}

/* 确保深色模式下的输入框样式 */
.dark-mode input, .dark-mode textarea, .dark-mode select {
  background-color: var(--card-background);
  color: var(--text-color);
  border-color: var(--border-color);
}

/* 确保深色模式下的按钮样式 */
.dark-mode button {
  background-color: var(--button-background, var(--card-background));
  color: var(--button-text, var(--text-color));
  border-color: var(--button-border, var(--border-color));
}

/* 确保深色模式下的卡片样式 */
.dark-mode .card, .dark-mode [class*="Card"], .dark-mode [class*="card"] {
  background-color: var(--card-background);
  color: var(--text-color);
  border-color: var(--border-color);
}

/* 确保深色模式下的图标颜色 */
.dark-mode svg {
  color: var(--icon-color, var(--text-color));
}

/* 确保深色模式下的模态框样式 */
.dark-mode .modal, .dark-mode [class*="Modal"], .dark-mode [class*="modal"] {
  background-color: var(--card-background);
  color: var(--text-color);
  border-color: var(--border-color);
}

/* 确保深色模式下的表格样式 */
.dark-mode table {
  color: var(--text-color);
  border-color: var(--border-color);
}

.dark-mode th, .dark-mode td {
  border-color: var(--border-color);
}

/* 确保深色模式下的列表样式 */
.dark-mode ul, .dark-mode ol {
  color: var(--text-color);
}

/* 确保深色模式下的分割线样式 */
.dark-mode hr {
  border-color: var(--border-color);
}

/* 确保深色模式下的滚动条样式 */
.dark-mode ::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.dark-mode ::-webkit-scrollbar-track {
  background: var(--background-color);
}

.dark-mode ::-webkit-scrollbar-thumb {
  background-color: var(--border-color);
  border-radius: 4px;
}

.dark-mode ::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-secondary-color);
}